<template>
  <div class="main_navs">
    <Header></Header>
    <section class="prof">
      <div class="container">
        <h1 class="h1_main">
          <span>{{$t('tit1')}}</span><br>
            {{$t('tit2')}}
          <span>{{ $t('tit3') }}</span>
        </h1>
        <div class="row filter">
          <div class="col-xl-6 input_text_f">
            <div class="input_filt">
              <input type="text" @input="getProf_search()" v-model="search" :placeholder="$t('search')">
              <div class="icon_f">

               {{$t('search')}}

              </div>
            </div>
          </div>
          <div class="col-xl-3 filt_f">
            <model-select :options="options"
                          v-model="item"
                          :placeholder="$t('kasb2')"
                          @input="getProf_filt()"
            >
            </model-select>
          </div>

          <div class="col-xl-3 filt_f1">
            <model-select :options="options1"
                          v-model="item1"
                          :placeholder="$t('salery')"
                          @input="getProf_filt1()"
            >
            </model-select>
          </div>
        </div>
        <div class="row">
          <div class="col-xl-6" v-for="(item,ind) in $store.state.professions_web.results" :key="ind">
            <div class="prof_card">
              <div class="col-xl-2">
                <img class="cover_img" :src="item.cover_image_url" alt="">
              </div>
                <div class="col-xl-10 card_actions">
                  <div class="prof_title mb-3">{{item.title}}</div>
                  <div class="prof_desc" v-html="item.workplace"></div>
                  <div class="prof_fa">
                    <div class="prof_price">{{$t('salery')}}: {{item.salary_range}}</div>
                    <div class="prof_link">
                      <router-link class="decoration" :to="`/professions/${item.id}`">{{$t('more')}}</router-link>
                    </div>
                  </div>
                </div>
            </div>
          </div>
        </div>
        <div class="pagenationss" v-if="$store.state.professions_web.results">
          <b-pagination
              v-model="page"
              :total-rows="$store.state.professions_web.total_pages"
              :per-page="perPage"
              first-number
              last-number
              aria-controls="my-table"></b-pagination>
        </div>
      </div>
    </section>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../components/Header";
import Footer from "../components/Footer";
import {ModelSelect} from 'vue-search-select'

export default {
  components: {Footer, Header, ModelSelect},
  data() {
    return {
      page: 1,
      total: 2,
      perPage:1,
      options: [
        {
          value: null,
          text: 'Barcha kasblar'
        }
      ],
      item: {
        value: '',
        text: ''
      },
      search:'',
      options1: [
        {
          value: 'ordering=min_salary',
          text: 'Eng kam maosh'
        },
        {
          value: 'ordering=max_salary',
          text: 'Eng yuqri maosh'
        },
      ],
      item1: {
        value: '',
        text: ''
      },
      profession_category:[]
    }
  },
  mounted() {
    this.$store.dispatch('getProfision',1)
        this.$axiosDefault.get(`profession-categories/`,
            {
              headers:{
                'Accept-Language':localStorage.getItem('lang')
              }
            })
        .then(res=>{
          this.profession_category=res.data
          this.profession_category.forEach(el=>{
            this.options.push({
              text:el.title,
              value:el.id
            })
          })

        })
  },
  methods:{
    getProf_filt(){
      if(this.item.value!=null){
        this.$store.dispatch('getProfisionFilt',`professsion-category=${this.item.value}`)
      }
      else {

        this.$store.dispatch('getProfision',1)
      }

    },
    getProf_filt1(){
        this.$store.dispatch('getProfisionFilt',`${this.item1.value}`)
    },
    getProf_search(){
      this.$store.dispatch('getProfisionFilt',`search=${this.search}`)
    }
  },
  watch: {
    page(page) {
      this.$store.dispatch('getProfision', page)
      window.scrollTo(0,0);
    },
  },
}
</script>
